
.modules-panel{position:absolute;left:0;right:0;bottom:0;top:0;padding-left:8px;box-sizing:border-box
}
.modules-panel ul{list-style:none
}
.modules-panel ul:before,.modules-panel ul:after{content:" ";display:table;clear:both
}
.modules-panel ul li{width:30px;height:22px;float:left;border-radius:3px;box-sizing:border-box;position:relative;margin-right:5px;overflow:hidden
}
.modules-panel ul li .inner{position:absolute;left:0;right:0;top:0;bottom:0;box-sizing:border-box;border-radius:3px
}
.modules-panel ul li.hover{box-shadow:0px 1px 0px #636363
}
.modules-panel ul li.hover .inner{border:1px solid #2b2b2b;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#757575), to(#636363));box-shadow:0px 1px 0px #636363
}
.modules-panel ul li.active{box-shadow:0px 1px 0px #636363
}
.modules-panel ul li.active .inner{border:1px solid #2b2b2b;background:#393939;box-shadow:0px 2px 2px #2b2b2b inset
}
.modules-panel ul i{font-size:14px;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}



