
.module-border{position:absolute;left:0;right:0;bottom:0;top:0;z-index:2
}
.module-border .bd-gray{position:absolute;left:0;top:0;right:0;bottom:0;border:1px solid #d0d0d0;z-index:1
}
.module-border .bd-active{position:absolute;left:0;top:0;right:0;bottom:0;border:1px solid #98b9fd;z-index:2
}
.module-border .bd-active span{position:absolute;width:6px;height:6px;border:1px solid #000;box-sizing:border-box;background-color:#fff
}
.module-border .bd-active span.lt{left:-3px;top:-3px
}
.module-border .bd-active span.ct{left:50%;top:-3px;transform:translate(-50%, 0)
}
.module-border .bd-active span.rt{right:-3px;top:-3px
}
.module-border .bd-active span.rc{right:-3px;top:50%;transform:translate(0, -50%)
}
.module-border .bd-active span.rb{right:-3px;bottom:-3px
}
.module-border .bd-active span.bc{left:50%;bottom:-3px;transform:translate(-50%, 0)
}
.module-border .bd-active span.bl{left:-3px;bottom:-3px
}
.module-border .bd-active span.lc{left:-3px;top:50%;transform:translate(0, -50%)
}



