
.stage-tab-switch{height:19px;background-color:#393939;border-bottom:1px solid #282828;position:relative;box-shadow:0 2px 7px #000;position:absolute;left:0;right:0;top:0;z-index:100
}
.stage-tab-switch .line{position:absolute;left:0;right:0;border-top:1px solid #4a4a4a;z-index:1
}
.stage-tab-switch ul{height:100%;overflow:hidden;position:absolute;left:0;right:0;top:0;bottom:0;z-index:2;display:-ms-box;display:-webkit-box;display:box;display:-ms-flexbox;display:flex
}
.stage-tab-switch li{max-width:200px;height:100%;background-color:#383838;font-size:0;position:relative;float:left;border-right:1px solid #282828;-ms-box-flex:1;box-flex:1;-ms-flex:1;flex:1
}
.stage-tab-switch li.active{background-color:#535353
}
.stage-tab-switch li .icon-tab-close{position:absolute;left:8px;top:50%;transform:translateY(-50%)
}
.stage-tab-switch .inner{height:100%;box-sizing:border-box;border-top:1px solid #4a4a4a;padding-left:22px
}
.stage-tab-switch li.active .inner{border-top:1px solid #6a6a6a
}
.stage-tab-switch p{font-size:12px;color:#a6a6a6;line-height:18px
}
.stage-tab-switch .active p{color:#e7e7e7
}



