
.tool-box{position:absolute;width:38px;left:0;top:55px;bottom:0;background-color:#535353;overflow:hidden;border-top:1px solid #282828;border-right:1px solid #282828;box-sizing:border-box
}
.tool-box ul{width:100%;height:100%;font-size:0;padding:17px 3px 0;box-sizing:border-box
}
.tool-box ul li{margin-bottom:2px
}
.tool-box .inner{box-sizing:border-box;height:24px;position:relative
}
.tool-box .inner i{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:14px
}
.tool-box li.active,.tool-box li.hover{box-shadow:0px 1px 0px #636363;border-radius:3px
}
.tool-box li.active .inner{border:1px solid #2a2a2a;border-radius:3px;background-color:#3b3b3b;box-shadow:0px 2px 2px #2b2b2b inset
}
.tool-box li.hover .inner{background-color:#727272;border:1px solid #2a2a2a;border-radius:3px;box-shadow:0px 1px 0px #909090 inset;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#757575), to(#636363))
}



