
.win-tab{height:24px;background-color:#393939;border-bottom:1px solid #6a6a6a;box-sizing:border-box
}
.win-tab ul{list-style:none;height:100%;box-sizing:border-box;border-bottom:1px solid #282828;background:url(./tabbg.jpg) repeat-x left 0;background-size:auto 1px
}
.win-tab ul:before,.win-tab ul:after{content:" ";display:table;clear:both
}
.win-tab ul li{float:left;font-size:12px;color:#a6a6a6;height:100%;padding:0 7px;line-height:24px;position:relative;border-right:1px solid #282828
}
.win-tab ul li.active{color:#fff;background:#535353 url(./tabactive.jpg) repeat-x left 0;background-size:auto 1px
}
.win-tab ul li.active:hover{background:#535353 url(./tabactive.jpg) repeat-x left 0;background-size:auto 1px;color:#fff
}
.win-tab ul li.active::after{content:' ';position:absolute;width:100%;height:4px;background-color:#535353;left:0;bottom:-2px
}
.win-tab ul li:hover{background:#454545 url(./hoverbg.jpg) repeat-x left 0;background-size:auto 1px
}



