
.map-area-win .end-time-label{font-size:11px;color:#fff;line-height:1;display:block
}
.map-area-win .date .el-input{box-shadow:0px 1px 0px #636363;border-radius:4px
}
.map-area-win .date .el-input__icon{color:#fff
}
.map-area-win .date .el-input__inner{height:22px;box-sizing:border-box;border:1px solid #2d2d2d;background-color:#4a4a4a;color:#fff;font-weight:lighter;box-shadow:0px 1px 1px #373737 inset;border-radius:4px;font-size:12px
}
.map-area-win .date .el-input__inner::-webkit-input-placeholder{color:#fff;font-weight:lighter;font-size:12px
}
.zf-date-picker{background-color:#535353;border:1px solid #2d2d2d;box-shadow:0 2px 10px #000;color:#fff
}
