}
.zf-date-picker .el-picker-panel__footer{background-color:#535353;border-top:1px solid #2d2d2d;box-shadow:0px 1px 0px #636363 inset
}
.zf-date-picker .el-input__inner{height:22px;box-sizing:border-box;border:1px solid #2d2d2d;background-color:#4a4a4a;color:#fff;font-weight:lighter;box-shadow:0px 1px 1px #373737 inset;border-radius:4px;font-size:12px
}
.zf-date-picker .el-picker-panel__icon-btn{color:#fff
}
.zf-date-picker .el-date-table th{color:#fff
}
