}
.zf-date-picker .el-date-table td.available:hover{color:#fff;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#757575), to(#636363));border:1px solid #2a2a2a;border-radius:3px
}
.zf-date-picker .el-date-table td.current{background-color:#20a0ff !important;border-radius:3px
