
.zf-checkbox{overflow:hidden
}
.zf-checkbox .el-checkbox__input{float:left;line-height:32px;height:32px;position:relative;width:18px
}
.zf-checkbox .el-checkbox__input .el-checkbox__inner,.zf-checkbox .el-checkbox__input.is-checked .el-checkbox__inner{border:1px solid #2b2b2b;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#747474), to(#626262));box-shadow:0px 1px 0px #636363;overflow:hidden;position:absolute;float:left;top:50%;transform:translate(0, -50%)
}
.zf-checkbox .el-checkbox__input .el-checkbox__inner::before,.zf-checkbox .el-checkbox__input.is-checked .el-checkbox__inner::before{content:'';border-top:1px solid #909090;position:absolute;top:0px;left:0;right:0
}
.zf-checkbox .el-checkbox__label{font-size:12px;color:#fff;float:left;line-height:32px
}



