
.zf-slider .el-slider__runway{box-shadow:0px 1px 2px #000 inset;background-color:#fff
}
.zf-slider .el-slider__bar{background-color:#393939;background-color:#1b83fb;box-shadow:0px 1px 1px #000 inset;border-bottom:1px solid #636363
}
.zf-slider .el-slider__button{background-color:#1b83fb;box-shadow:1px 1px 2px #000
}
.zf-slider .el-slider{position:relative
}
.zf-slider .el-input-number{width:50px;position:absolute;right:0;top:50%;transform:translate(0, -50%);margin:0
}
.zf-slider .el-slider__runway{margin-right:60px
}
.zf-slider .el-input__inner{background-color:#4a4a4a;color:#fff;border:1px solid #2d2d2d;box-shadow:0px 1px 1px #373737 inset;box-sizing:border-box;height:22px;padding:3px 5px;text-align:center;line-height:1
}
.zf-slider .zf-slider-item{padding-left:20px;position:relative
}
.zf-slider .zf-slider-item label{position:absolute;left:0;top:50%;transform:translate(0, -50%);color:#fff;font-size:12px
}
.el-tooltip__popper.is-dark{background-color:#252525
}
.el-tooltip__popper.is-dark .popper__arrow::after{border-top-color:#252525
}



