
.el-select.zf-sel-def{overflow:hidden;box-shadow:0px 1px 0px #636363;border-radius:4px
}
.el-select.zf-sel-def input{width:80px;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#747474), to(#636363));border:1px solid #2d2d2d;color:#fff;font-size:11px;padding:3px 0 3px 6px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;height:22px;box-sizing:border-box
}
.el-select.zf-sel-def:hover .el-input__inner,.el-select.zf-sel-def:focus .el-input__inner{border:1px solid #2d2d2d
}
.el-select.zf-sel-def .el-input__inner:focus{border:1px solid #2d2d2d
}
.el-select.zf-sel-def .el-input__icon{transition:none;width:14px;height:14px;right:4px;transform:scale(0.9) translateY(-50%) rotateZ(180deg);color:#cacaca
}
.el-select.zf-sel-def .el-input__icon.is-reverse{transform:scale(0.9) translateY(-50%)
}
.el-select-dropdown.opt-def{background:#535353;border:1px solid #2d2d2d;border-radius:4px;overflow:hidden
}
.el-select-dropdown.opt-def .el-select-dropdown__list{padding:0
}
.el-select-dropdown.opt-def .el-select-dropdown__item{color:#fff;font-size:12px;height:auto;text-align:left;padding:4px 10px;background:#535353
}
.el-select-dropdown.opt-def .el-select-dropdown__item.selected.hover,.el-select-dropdown.opt-def .el-select-dropdown__item.selected{background:#3b3b3b
}
.el-select-dropdown.opt-def .el-select-dropdown__item.hover{background:#4a4a4a
}



