
.index-change{font-size:12px;color:#fff
}
.index-change label{float:left;line-height:24px;padding-right:5px
}
.index-change .btn{width:31px;height:24px;box-sizing:border-box
}
.index-change .btn .inner{position:relative;box-sizing:border-box;width:100%;height:100%
}
.index-change .btn .inner i{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}
.index-change .btn.hover{box-shadow:0px 1px 0px #636363;border-radius:3px
}
.index-change .btn.hover .inner{background-color:#727272;border:1px solid #2a2a2a;border-radius:3px;box-shadow:0px 1px 0px #909090 inset;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#757575), to(#636363))
}
.index-change .btn.active{box-shadow:0px 1px 0px #636363;border-radius:3px
}
.index-change .btn.active .inner{border:1px solid #2a2a2a;border-radius:3px;background-color:#3b3b3b;box-shadow:0px 2px 2px #2b2b2b inset
}



