
.zf-module-pic{position:absolute;left:0;top:0;right:0;bottom:0;z-index:1
}
.zf-module-pic img{position:absolute;left:0;top:0;width:100%;height:100%;z-index:2
}
.zf-module-pic .loading-img{font-size:40px;width:100%;height:100%
}
.zf-module-pic .loading-img .el-icon-picture{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}
.zf-module-pic .loading-img .el-loading-spinner .path{stroke:#535353
}
.zf-module-pic .loading-img .el-loading-text{color:#535353;font-size:14px
}



