
body,html{font-family:Helvetica, sans-serif;background-color:#262626
}
html,body,div,span,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,address,big,cite,code,del,em,font,img,ins,small,strong,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend{margin:0;padding:0
}
article,aside,details,figcaption,figure,footer,header,hgroup,main,nav,section,summary{display:block
}
audio,canvas,progress,video{display:inline-block;vertical-align:baseline
}
img{border:0;vertical-align:middle
}
a,a:link{color:#333;text-decoration:none
}
a:hover{color:#333;text-decoration:underline
}
.p10{padding:10px
}
.p20{padding:20px
}
.pr7{padding-right:7px
}
.pr10{padding-right:10px
}
.pr20{padding-right:20px
}
.pl5{padding-left:5px
}
.pl10{padding-left:10px
}
.pl11{padding-left:11px
}
.pb5{padding-bottom:5px
}
.pb10{padding-bottom:10px
}
.pt4{padding-top:4px
}
.pt5{padding-top:5px
}
.pt10{padding-top:10px
}
.pt20{padding-top:20px
}
.fl{float:left
}
.fr{float:right
}
.fn{float:none
}
.al{text-align:left
}
.ac{text-align:center
}
.ar{text-align:right
}
.ov{overflow:hidden
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden
}
.clearfix{*zoom:1
}
.hare-app{position:absolute;left:0;top:0;right:0;bottom:0;cursor:Default;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;background-color:#3a3a3a;box-sizing:border-box
}
.hare-app .hare-content{left:0;right:293px;position:absolute;bottom:0;top:55px;box-sizing:border-box
}
.hare-app .hare-window{position:absolute;width:293px;top:55px;right:0;bottom:0;border-top:1px solid #282828;border-left:1px solid #282828;background-color:#535353;box-sizing:border-box
}
.hare-app .stage-box{position:relative;width:100%;height:100%
}



