
.scale-btns-wrap:before,.scale-btns-wrap:after{content:" ";display:table;clear:both
}
.scale-btns-wrap ul{list-style:none
}
.scale-btns-wrap ul:before,.scale-btns-wrap ul:after{content:" ";display:table;clear:both
}
.scale-btns-wrap ul li{width:22px;height:22px;float:left;border-radius:3px;box-sizing:border-box;position:relative;margin-right:2px
}
.scale-btns-wrap ul li.hover{border:1px solid #2b2b2b;background:-webkit-gradient(linear, 0% 0%, 0% 100%, from(#757575), to(#636363));box-shadow:0px 1px 0px #636363
}
.scale-btns-wrap ul li.active{border:1px solid #2b2b2b;background:#393939;box-shadow:0px 2px 2px #2b2b2b inset
}
.scale-btns-wrap ul li i{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}



