
.win-btns{position:absolute;overflow:hidden;top:50%;left:8px;margin-top:-6px
}
.win-btns li{width:12px;height:12px;border-radius:50%;float:left;margin-right:8px;position:relative
}
.win-btns:last-child{margin-right:0
}
.win-btns .close{background-color:#fc625d
}
.win-btns .close.active span{display:block
}
.win-btns .close span{display:none;width:6px;height:6px;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);background:url(./closeActive.jpg) no-repeat center center;background-size:6px 6px
}
.win-btns .min{background-color:#fdc041
}
.win-btns .min.active span{display:block
}
.win-btns .min span{display:none;width:8px;border-top:1px solid #985712;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}
.win-btns .max{background-color:#34c849
}
.win-btns .max.active span{display:block
}
.win-btns .max span{display:none;width:6px;height:6px;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);background:url(./max.jpg) no-repeat center center;background-size:100% 100%
}



