
.zf-module-tick{position:absolute;left:0;top:0;right:0;bottom:0;z-index:1
}
.zf-module-tick .time-wrap{height:48px;text-align:center;font-size:0;width:100%
}
.zf-module-tick .time-wrap span{display:inline-block;vertical-align:bottom
}
.zf-module-tick .time-wrap .num{width:30px;background-color:#000;border-radius:4px;text-align:center;line-height:48px;color:#fff;font-size:37px
}
.zf-module-tick .time-wrap .txt{color:#fff;font-size:21px;line-height:48px;height:48px;width:25px
}
.zf-module-tick .time-wrap .num.ms{background-color:#000;color:#fff
}
.zf-module-tick .time-wrap .one{margin-right:2px
}



